/* ---------------Footer------------------------- */
.footer {
  display: flex;
  justify-content: center;
  margin-bottom: 3rem;
  padding-top: 10vh;
}

.iconsRow {
  /* width: 40vw; */
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  /* text-align: center; */
  /* align-items: center; */
}

.messageIconAnimate {
  transition: border 0s 0.25s;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-width: 65px;
  min-height: 65px;
  background-color: #00d4d000;
  border-radius: 50%;
  cursor: pointer;
  z-index: 3;
  border: none;
  /* margin-left: auto;
  margin-right: auto; */
}
.icons {
  transition: 0.3s;
}

@media (max-width: 360px) {
  .icons,
  .messageIconAnimate {
    margin: 0 0.5rem;
  }
}

@media (min-width: 360px) {
  .icons,
  .messageIconAnimate {
    margin: 0 1.5rem;
  }
}

.icons:hover {
  transform: translate(0, -10px);
}

.upArrow {
  width: 5rem;
  display: flex;
  margin-left: auto !important;
  margin-right: auto !important;
  cursor: pointer;
}

.arrowContainer {
  width: 100%;
}

