.messageIcon {
  /* transition: top 0.5s, transform .5s .25s, border-radius .5s .25s, background-color .5s .25s, border-color .5s .25s, border-style .5s .25s, border-width .5s .25s; */
  /* transition: all .5s; */
  position: absolute;
  width: 65px;
  height: 65px;
  border-radius: 50%;
  cursor: pointer;
  opacity: 1;
  display: flex;
  background-color: #00d4d0;
  justify-content: center;
  align-items: center;
  text-align: center;
  z-index: 2;
  top: 0vh;
  border-color: transparent;
  border-style: none;
  border-width: 0px 0px;
  margin-bottom: 0px;
  margin-left: auto;
  margin-right: auto;
}

.active + .messageIcon {
  transition: top 0.5s, transform 0.5s 0.25s, border-radius 0.5s 0.25s,
    background-color 0.5s 0.25s, border-color 0.5s 0.25s,
    border-style 0.5s 0.25s, border-width 0.5s 0.25s, width .25s .25s, height .25s .25s;
    width: 100vw;
    height: 7vh;
}

.inactive + .messageIcon {
  transition: top 0.5s 0.25s, transform 0.5s, border-radius 0.5s,
    background-color 0.5s, border-color 0.5s, border-style 0.5s,
    border-width 0.5s;
}

.active:hover + .messageIcon {
  top: -18vh;
  transform: scale(8, 2.8);
  border-radius: 5px / 30px;
  background-color: #00d4d010;
  border-color: #00d4d0;
  border-style: solid;
  border-width: 2px 1.2px;
}

.contactWrapper {
  position: absolute;
  max-width: 65px;
  max-width:65px;
  opacity: 0;
  z-index: 0;
  top: -20vh;
  left: 50%;
  transform: translateX(-50%);
  right: 0;
  bottom: 0;
  color: white;
}


.active + .messageIcon + .contactWrapper {
  transition: all 0s .55s;
  max-width: 100%;
  max-width:100%;
  opacity: 1;
}

.inactive + .messageIcon + .contactWrapper {
  transition: all 0s;
  opacity: 0;
}

.contactRow .bars {
background-color: transparent;
width: 40px;
height: 40px;
padding: 10px;
border-radius: 50%;
box-shadow: 0px 2px 22px -4px #a0e0df31;
border: 1.5px solid #00d4d0;
}


.contactRow > p {
  white-space: nowrap;
}
