.messageIconAnimate:hover {
  border: 2px dashed white;
}

.messageIconAnimate:hover > .messageIcon * {
  display: none;
}

.inactive + .messageIcon > .bars {
  opacity: 1;
}

.active + .messageIcon > .bars {
  transition: opacity 0s;
  opacity: 0;
}

.inactive + .messageIcon > .bars {
  transition: opacity 0s .25s;
  opacity: 1;
}
/* .messageIconAnimate:hover + .messageIcon > .bars {
  opacity: 0;
} */

.messageIconAnimate + .messageIcon {
  background-color: #00d4d0;
}


